
.wrap {
  margin-top: 10px;
}
.wrap .list {
  border-bottom: 1px solid #fcfcfc;
}
.wrap .list header {
  height: 40px;
  line-height: 40px;
  background: #41b884;
  color: #fff;
  font-size: 15px;
}
.wrap .list header i {
  float: right;
  padding-right: 10px;
}
.wrap .list .list-data li {
  width: 100%;
  min-height: 40px;
  line-height: 40px;
  padding: 0 6px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 15px;
  background: #f9f9f9;
  border-bottom: 1px solid rgba(15, 25, 35, 0.1);
}
.wrap .list .list-data li span.content {
  -webkit-box-flex: 1;
      -ms-flex: 1 0 150px;
          flex: 1 0 150px;
  width: 150px;
  padding: 0 10px;
}
.wrap .list .list-data li span.content.cancel {
  text-decoration: line-through;
  color: #ccc;
}
.wrap .list .list-data li .time {
  font-size: 12px;
  color: #999;
}
.wrap .list .list-data li button {
  line-height: 25px;
  width: 40px;
  height: 25px;
  border: 0;
  margin-right: 5px;
  font-size: 11px;
  color: #fff;
  border-radius: 2px;
  background: rgba(15, 25, 35, 0.6);
}
.wrap .list .list-data li button.edite {
  background: #2472c8;
}
.wrap .list .list-data li button.refresh {
  background: #fcb738;
}
.wrap .list .list-data li button.delete {
  background: #e94242;
}



