
.add {
  padding: 16px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.add input {
  width: 280px;
  height: 33px;
  line-height: 33px;
  -webkit-box-flex: 1;
      -ms-flex: 1 1 280px;
          flex: 1 1 280px;
  padding-left: 10px;
  border: 1px solid #dcdcdc;
  border-radius: 2px;
}
.add input:focus {
  outline: 0;
}
.add button {
  margin-left: 3px;
  width: 80px;
  -webkit-box-flex: 0;
      -ms-flex: 0 1 80px;
          flex: 0 1 80px;
  background: #0dbc79;
  border: 0;
  color: #fff;
  border-radius: 2px;
}
.add button:focus {
  outline: 0;
}
.add button:hover {
  border: none;
  box-shadow: 1px 0 2px #999;
}



