
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.clearAll{
  height: 40px;
  width: 60%;
  display: block;
  margin: 180px auto 0;
  border: 0;
  border-radius: 4px;
  color: #fff;
  background: rgb(233,40,40);
}
button:focus{
  outline: none;
  background: rgb(240,60,40);
}



